
body {
    padding: 0;
    margin: 0;
    height: 100%;
    width: 100%;
    background-color: #f6f6f6;
}
.container {
    width: 90%;
    margin: 0 auto;
}
.waterfall {
    -webkit-column-count: 3;
    column-count: 3;
    -webkit-column-width: 24em;
    column-width: 24em;
    -webkit-column-gap: 1em;
    column-gap: 1em;
}
.img-cell {
    padding: 1em;
    margin: 0 0.125em 1em;
    -webkit-column-break-inside: avoid;
    break-inside: avoid;
    background: white;
    -webkit-transition: all .3s;
    transition: all .3s;
}
.img-cell:hover {
    -webkit-transform: translateY(-3px);
            transform: translateY(-3px);
    -webkit-box-shadow: 1px 1px 20px #999;
            box-shadow: 1px 1px 20px #999;
}
.img-cell img {
    width: 100%;
    padding-bottom: 1em;
    margin-bottom: 0.5em;
}
